async function addComment( content ) {
    try {
        const issue    = new Issue();
        const iss_res  = await issue.fetch( issue.getId( issue.memos ));
        const iss_data = await iss_res.json();
        const issueId  = iss_data.data.repository.issue.id;
        const comment  = new Comment();
        const cmm_res  = await comment.fetch( comment.add( issueId, content ));
        const result   = await cmm_res.json();
        return new Promise(( resolve, reject ) => {
            if ( result ) {
                resolve( result );
            } else {
                reject( result );
            }
        });
    } catch ( error ) {
        return new Promise(( resolve, reject ) => reject( error ));
    }
}

module.exports = {
    getUser, getIssues, getIssue, addComment, getMemos
};